:root {
    --main-backgroung-color: #212121;
    --main-font-color: #EEEEEE;
    --light-blue: #BBE1FA;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: var(--main-font-color);
    font-family: Tajawal;
}

body {
    background-color: var(--main-backgroung-color);
}

.container {
    width: 50vw;
    margin: auto;
}